	module.exports = {"data":{"markdownRemark":{"html":"<p><strong>Do you like staying busy? Good!</strong></p>\n<p>This is not a \"look at Facebook job\". You'll be in charge of making sure our office runs smoothly üõµ. On any given day you'll be working with external partners, going shopping for the office, helping set up for an event, working with an outside accountant, handling payments and being a face üíÅüèΩ‚Äç üë®üèΩ‚Äçüíº of our brand.</p>\n<h2>The job</h2>\n<ul>\n<li>Respond to phone, email, Facebook/Viber/WhatsApp chats</li>\n<li>Manage our learning space service (payments, food &#x26; beverages stocked, office supplies, etc.)</li>\n<li>Work with external partners</li>\n<li>Manage opening and closing of office</li>\n<li>Coordinate payroll</li>\n<li>Make payments to suppliers</li>\n<li>Make trips to the bank</li>\n<li>In tax season, working with our auditor and accountant</li>\n<li>Coordinate office enhancements and repairs</li>\n</ul>\n<h2>Requirements</h2>\n<ul>\n<li>1+ year of relevant experience (freshers üéì can apply)</li>\n<li><strong>You can get things done on time</strong></li>\n<li><strong>You can coordinate multiple tasks</strong></li>\n<li>You're a strong and confident communicator</li>\n<li>You're a problem solver at heart</li>\n<li>You are computer literate (knowledge of Mac computers is a big plus)</li>\n<li>Interest in tech and what we do (hint: we teach programming)</li>\n<li>Experience in a startup is viewed favourably</li>\n</ul>\n<h2>Salary</h2>\n<ul>\n<li>Depends on your experience</li>\n<li>Starting salary is Rs.12,500 per month</li>\n</ul>\n<h2>Application process</h2>\n<ol>\n<li>Informal chat about you and what we do</li>\n<li>If you're a good fit, you'll be given a fun assessment to complete in our office</li>\n<li>We'll talk a bit and get back to you ASAP with our decision.</li>\n<li>If all is well, there will be a 2 month probationary period, just to make sure we're working well together.</li>\n</ol>\n<h2>How to apply</h2>\n<ul>\n<li>Send your CV to: <span class=\"font__highlight\"><b>jobs@codeleadgrow.com</b></span>\n<br />(subject should be \"Office Coordinator Application\")</li>\n<li>Please <strong>do not</strong> apply via Facebook</li>\n</ul>","frontmatter":{"title":"Office Coordinator","path":"/jobs/office-coordinator","excerpt":null}}},"pathContext":{}}

